<template>
    <div style="text-align: center;" class="btile">
        <h2>오 늘 식 수  [ {{ today_s }} ]</h2>
    </div>
    <div table-responsive>
        <table class="table table-sm table-hover">
            <thead class="thead-dark">
                <tr>
                    <th scope="col">성   명</th>
                    <th scope="col">계정메일</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="user in this.users" :key="user.uid">
                    <td scope="row">{{user.username}}</td>
                    <td scope="row">{{user.email}}</td>                    
                </tr>
            </tbody>
        </table>
    </div>
    <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message" >
                {{ this.message }}
    </div>
    <div> <h2>전체 식수 : {{ this.ticketLength }} 명</h2></div>
    <div class="footer">
        <div class="d-flex justify-content-center">
            <b-button class="btn" variant="primary" to="/home" size="lg" >홈</b-button>
        </div>
    </div> 
    <!-- <b-button to="/home" size="sm" variant="success">홈</b-button> -->
    <!-- <div v-for="user in this.users" :key="user.id"> {{ user }}</div>         -->
    
</template>

<script>
import {ref} from 'vue';
const in_ticketLength = ref(0);
const in_users=ref([]);

export default {
    name: "TicketCount",
    components: { 
        // Header
    },
    data() {
        return{
            ticketLength: in_ticketLength,
            users: in_users,
            message: "",
            today_s: "",
        }        
    },
    mounted() {
        this.$store.dispatch("fetchAllTicketList");
        this.$store.dispatch("fetchAlluserList");
        const mydate= new Date();        
        this.today_s = mydate.getFullYear()+'년  '+mydate.getMonth()+'월  '+mydate.getDate()+'일';
    },
    watch:{
        getAllTicketList(val){
            if ( val.length > 0) {
                let usersArray=[];
                this.ticketLength = val.length;
                let users = this.$store.state.auth.userList;                        
                val.forEach( (ticket) =>{
                    var hasname = e => e.uid === ticket.uid;
                    let uname= users.find(hasname);
                    usersArray.push(uname);
                });
                this.users = usersArray;
            }
        },
        getAlluserList(val) {
            this.userList = val;            
        }
    },
    computed: {
        getAllTicketList() {        
            return this.$store.state.tickets.allticketList;            
        },
        getAlluserList() {
            return this.$store.state.auth.userList;
        }
    }
}
</script>

<style>
.footer{
    padding: 30px;
}
.btile{
    padding: 20px;
}

</style>